body {
  margin: 0;
  background: #CC99FF;
  background-size: cover;
  font-family: 'Josefin Sans', sans-serif;
  color: #333;
  width: 100%;
  height: 100%;
}

#root {
  margin: 0px;
  width: 100%;
  height: 100%;
}

.app-whole{
  margin: 0px;
  width: 100%;
  height: 100%;
  display: flexbox;
  justify-content: space-evenly;
}

.nav-bar {
  justify-content: space-evenly;
  width: 100%;
  margin-top: 5px;
  margin-bottom: 5px;
  padding: 0;
  opacity: 0;
}
.nav-bar:hover {
  opacity: 1;
  transition: all .5s ease-in-out;
}
 
 .nav-bar:target {
  height: auto;
}

.home-whole {
  height: 100%;
  width: 100%;
  text-align: center;
  margin-top: 5%;
}


h1 {
  font-size: 10em;
  margin: 0;
}

h2 {
  font-size: 9em;
  margin: 0;
}

h3 {
  font-size: 8em;
  margin: 0;
  font-style: italic;
}

h4 {
  font-size: 7.5em;
  margin: 0;
}

h5 {
  font-size: 7em;
  margin: 0;
}
h6 {
  font-size: 6em;
  margin: 0;
}

i:hover {
  text-shadow: 2px 2px 5px whitesmoke;
}

.about {
  margin-left: 10%;
  margin-right: 10%;
  background: whitesmoke;
  border-radius: 10px;
  background:rgba(255, 255, 255, 0.2);
}
.about p {
  margin-left: 5%;
  margin-right: 5%;
  font-size: 3em;
  text-align: center;
}

